import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      	path: '/',
      	name: 'login',
      	component:resolve=>require(['@/components/login'],resolve),
        meta: {
            keepAlive:false
        }
    },
    {
        path: '/guide',
        name: 'guide',
        component:resolve=>require(['@/components/guide'],resolve),
        meta: {
            keepAlive:false
        }
    },
    {
      	path: '/nodeManage',
      	name: 'nodeManage',
      	component:resolve=>require(['@/components/nodeManage'],resolve),
        meta: {
            keepAlive:false
        }
    },
    {
        path: '/account',
        name: 'account',
        component:resolve=>require(['@/components/account'],resolve),
        meta: {
            keepAlive:false
        }
    },
    {
        path: '/resMonitor',
        name: 'resMonitor',
        component:resolve=>require(['@/components/resMonitor'],resolve),
        meta: {
            keepAlive:false
        }
    },
    {
        path: '/nodeSet',
        name: 'nodeSet',
        component:resolve=>require(['@/components/nodeSet'],resolve),
        meta: {
            keepAlive:false
        }
    },
    {
        path: '/senceSet',
        name: 'senceSet',
        component:resolve=>require(['@/components/senceSet'],resolve),
        meta: {
            keepAlive:false
        }
    },
    {
        path: '/dataDic',
        name: 'dataDic',
        component:resolve=>require(['@/components/dataDic'],resolve),
        meta: {
            keepAlive:false
        }
    },
    {
        path: '/dataManage',
        name: 'dataManage',
        component:resolve=>require(['@/components/dataManage'],resolve),
        meta: {
            keepAlive:false
        }
    },
    {
        path: '/dataWork',
        name: 'dataWork',
        component:resolve=>require(['@/components/dataWork'],resolve),
        meta: {
            keepAlive:true
        }
    },
    {
        path: '/modelTrain',
        name: 'modelTrain',
        component:resolve=>require(['@/components/modelTrain'],resolve),
        meta: {
            keepAlive:false
        }
    },
    {
        path: '/modelUse',
        name: 'modelUse',
        component:resolve=>require(['@/components/modelUse'],resolve),
        meta: {
            keepAlive:false
        }
    },
  ]
})



